// @ts-nocheck
// point icons
export const elevator = 'data:image/png;base64,';
export const escalator = 'data:image/png;base64,';
export const entrance = 'data:image/png;base64,';
export const hazard = 'data:image/png;base64,';
export const decision = 'data:image/png;base64,';
export const landmark = 'data:image/png;base64,';
export const poi = 'data:image/png;base64,';
export const staircase = 'data:image/png;base64,';
export const ticketGate = 'data:image/png;base64,';
export const door = 'data:image/png;base64,';
export const routeA = 'data:image/png;base64,';
export const routeB = 'data:image/png;base64,';
export const marker = 'data:image/png;base64,';
export const defaultIcon = poi;
export const chevron = 'data:image/png;base64,';
export const person = 'data:image/png;base64,';
export const floorchangeDownImage = 'data:image/png;base64,';
export const floorchangeUpImage = 'data:image/png;base64,';
export const popupImage = 'data:image/png;base64,';
const size = 120;
export const pulsingDot = {
    width: size,
    height: size,
    data: new Uint8Array(size * size * 4),
    // get rendering context for the map canvas when layer is added to the map
    onAdd(map, gl) {
        const canvas = document.createElement('canvas');
        canvas.width = this.width;
        canvas.height = this.height;
        this.context = canvas.getContext('2d');
        this.map = map;
    },
    // called once before every frame where the icon will be used
    render(gl, matrix) {
        const duration = 1000;
        const t = (performance.now() % duration) / duration;
        const radius = (size / 2) * 0.3;
        const outerRadius = (size / 2) * 0.7 * t + radius;
        const context = this.context;
        const map = this.map;
        // draw outer circle
        context.clearRect(0, 0, this.width, this.height);
        context.beginPath();
        context.arc(this.width / 2, this.height / 2, outerRadius, 0, Math.PI * 2);
        context.fillStyle = 'rgba(189,82,255,' + (1 - t) + ')';
        context.fill();
        // draw inner circle
        context.beginPath();
        context.arc(this.width / 2, this.height / 2, radius, 0, Math.PI * 2);
        context.fillStyle = 'rgb(189,82,255)';
        context.strokeStyle = 'white';
        context.lineWidth = 2 + 4 * (1 - t);
        context.fill();
        context.stroke();
        // update this image's data with data from the canvas
        this.data = context.getImageData(0, 0, this.width, this.height).data;
        // continuously repaint the map, resulting in the smooth animation of the dot
        map.triggerRepaint();
        // return `true` to let the map know that the image was updated
        return true;
    },
};
